/* src/components/HomePage.css */

.homepage {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 40px 80px 40px;
  background-color: #f8f2eb;
  /* min-height: 100vh; */
  flex-wrap: wrap;
  font-family: 'Arial', sans-serif;
}

.homepage-left,
.homepage-right {
  flex: 1 1 45%; /* Ocupa un 45% del ancho del contenedor */
  margin: 10px;
  box-sizing: border-box;
}


.logo {
  max-width: 350px;
  margin-bottom: 20px;
}

h1 {
  font-size: 2rem; /* Aumentar el tamaño del texto */
  color: #333;
  font-weight: 700;
  margin-top: 0px;
  
}

h1 span {
  color: #9e57ff; /* Color púrpura */
  font-size: 2rem; /* Hacer el texto dentro del span más grande */
}


p {
  font-size: 1.2rem;
  color: #707070;
  margin-bottom: 20px;
}

.input-container {
  display: flex;
  margin-top: 20px;
}

input {
  padding: 15px;
  font-size: 1rem;
  /* width: 300px; */
  border: 1px solid #000;
  border-radius: 5px 0 0 5px;
}
